import { useCallback, useMemo } from "react";
import ReactFlow, {
  Node,
  addEdge,
  Background,
  Edge,
  Connection,
  useNodesState,
  useEdgesState
} from "reactflow";

import "reactflow/dist/style.css";
import { MessageNode } from "../../Components/Nodes/MessageNode/MessageNode";


export const MessageFlow = () => {

  const initialNodes: Node[] = [
  {
    id: "1",
    type: 'messageNode',
    data: { label: "Node 1" },
    position: { x: 250, y: 5 },
  },
  { id: "2", type: 'messageNode', data: { label: "Node 2" }, position: { x: 100, y: 100 } },
  { id: "3", type: 'messageNode', data: { label: "Node 3" }, position: { x: 400, y: 100 } },
  { id: "4", type: 'messageNode', data: { label: "Node 4" }, position: { x: 400, y: 200 } }
];

const initialEdges: Edge[] = [
  { id: "e1-2", source: "1", target: "2", animated: false },
  { id: "e1-3", source: "1", target: "3" }
];

const nodeTypes = useMemo(() => ({ messageNode: MessageNode }),[]);
  
  const [nodes, , onNodesChange] = useNodesState(initialNodes);
  const [edges, setEdges, onEdgesChange] = useEdgesState(initialEdges);
  const onConnect = useCallback(
    (params: Edge | Connection) => setEdges((els) => addEdge(params, els)),
    [setEdges]
  );

  return (
    <ReactFlow
      nodeTypes={nodeTypes}
      nodes={nodes}
      edges={edges}
      onNodesChange={onNodesChange}
      onEdgesChange={onEdgesChange}
      onConnect={onConnect}
    >
      <Background color="white"/>
    </ReactFlow>
  );
};


